import styled from "styled-components";

import { colors, sizes, spacingMap } from "../../styleguide";
import Typography from "../../Typography/Typography";

export const LocalNavigationWrapper = styled.nav`
	box-sizing: border-box;
	position: fixed;
	left: ${sizes.globalNavigationWidth};
	top: ${sizes.menuBarHeightWithSpacing};
	bottom: 0;
	overflow: auto;
	border: solid 1px ${colors.lightGray};
	background: linear-gradient(${colors.lightGray}80, ${colors.lightGray}00);
	border-radius: ${spacingMap.sm};
	margin: ${spacingMap.sm} 0;
	ul {
   		height: fit-content;
  	}
	& li { // To position properly the subnav item active right bar
		position: relative;
	}
`;

export const LocalNavHeader = styled(Typography)`
	padding: ${spacingMap.sm} ${spacingMap.xs};
`;

export const LocalNavigationItemsWrapper = styled.ul`
	box-sizing: border-box;
	padding: 0 ${spacingMap.xs} ${spacingMap.sm} ${spacingMap.xs};
	height: 100%;
	margin: 0;
	width: ${sizes.localNavigationWidth};

	& a {
		display: block;
		text-decoration: none;

		&:focus-visible {
			opacity: 1;
			outline: 2px solid ${colors.midBlue};
		}

		&:hover {
			opacity: 1;
		};

		&:visited {
			color: initial;
			text-decoration: none;
		};

		+ ul { // subnav item
			opacity: 1;
			border: solid 1px transparent;
			border-top: 0;
			border-radius: 0 0 8px 8px;
		}

		&[aria-current="page"] + ul { // subnav item active
			opacity: 1;
			color: ${colors.midBlue};
			background-color: ${colors.white}80;
			border: solid 1px ${colors.white};
			border-top: 0;
			border-radius: 0 0 8px 8px;
		}

		&[aria-current="page"] + ul::after { // subnav item active right bar
			content: '';
			position: absolute;
			width: 4px;
			height: 100%;
			border-radius: 2px 0 0 2px;
			right: -8px;
			top: 0;
			opacity: 1;
			background-color: ${colors.midBlue};
			color: ${colors.midBlue};
		}
	}

	& li {
		list-style: none;
	}
`;
